@font-face{font-family:'Nunito';src:url(/two-doo/static/media/NunitoSans-Regular.87d92665.ttf)}*{box-sizing:border-box;margin:0;padding:0;font-family:"Nunito",sans-serif}html,body,#root{height:100%;max-width:100%}.btn{font-size:17.5px;padding:10px 15px;outline:none}.btn:hover{cursor:pointer}.page{height:100%;max-width:100%;display:grid;grid-template-rows:auto 1fr auto;background-color:#EDEBE8;color:#313036}.header{display:flex;flex-flow:column nowrap;max-width:100%}.header__pageinfo{background-color:#313036;width:100%;height:100%;padding:20px 20px;display:flex;flex-flow:row nowrap;justify-content:center;align-items:center}.header__title{width:100%;text-align:center;font-family:"Nunito",sans-serif;font-size:35px;color:#EDEBE8}.header__link{text-decoration:none;color:#EDEBE8;font-size:17.5px}.header__link:hover{text-decoration:underline}.header__link:link,.header__link:visited{color:#EDEBE8}.current-project{height:100%;width:100%;padding:5px 20px;display:flex;flex-flow:row nowrap;align-items:center;justify-content:flex-start}.current-project__info{display:flex;flex-flow:column nowrap;flex:9 1 auto;height:100%;width:100%}.current-project__title{font-size:28px;font-weight:bold;display:inline-block;flex:2 3 auto;width:80%;max-width:600px;overflow:hidden;text-overflow:ellipsis}.current-project__desc{font-size:21px;flex:1 1 auto;display:inline-block;max-width:500px;width:100%;text-overflow:ellipsis;white-space:nowrap;overflow:hidden}.current-project__edit{flex:1 1 auto;background-color:#e2dfda;border:1px solid #d7d2cb}.current-project__edit:hover{box-shadow:0 0 5px 1px #e7e5e1}.current-project__edit:active{box-shadow:inset 0 0 1px 1px #cbc6bd}.content{display:grid;grid-template-columns:200px 1fr;border-top:1px solid #d7d2cb;height:100%;width:100%}.sidebar{width:100%;height:100%;display:flex;flex-flow:column nowrap;border-right:1px solid #d7d2cb}.sidebar__new-project{background-color:#313036;border:1px solid #252429;color:#EDEBE8;width:80%;display:block;margin:auto;margin-top:10px}.sidebar__new-project:hover{box-shadow:0 0 5px 1px #8f8e8f}.sidebar__new-project:active{box-shadow:inset 0 0 1px 1px #19181b}.sidebar__projectlist{flex:1 1 auto;display:flex;flex-flow:column nowrap;align-items:center}.sidebar__project{background-color:#d7d2cb;border:1px solid #cbc6bd;display:block;margin-top:10px;width:80%;max-width:calc(80%);overflow:hidden;text-overflow:ellipsis}.sidebar__project:hover{box-shadow:0 0 5px 1px #e2dfda}.sidebar__project:active{box-shadow:inset 0 0 1px 1px #c0b9af}.sidebar__project--active{background-color:#806062;border:1px solid #715557}.sidebar__project--active:hover{box-shadow:0 0 5px 1px #b7a6a5}.sidebar__project--active:active{box-shadow:inset 0 0 1px 1px #634a4c}.todolist{display:flex;flex-flow:column nowrap;align-items:center;max-width:100%;padding:0px 10px;overflow:hidden}.todolist__add-todo{margin-top:10px;background-color:#313036;border:1px solid #252429;color:#EDEBE8}.todolist__add-todo:hover{box-shadow:0 0 5px 1px #8f8e8f}.todolist__add-todo:active{box-shadow:inset 0 0 1px 1px #19181b}.todo{display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;text-align:center;height:60px;width:100%;max-width:1200px;margin-top:10px;border-radius:15px;border:1px solid black;padding:10px;font-size:17.5px;background-color:#EDEBE8;color:#313036}.todo--done{background-color:#b5ada0;color:#313036}.todo--done>span{text-decoration:line-through}.todo__check{flex:0 0 auto;width:15px;height:15px;border-radius:15px;background-color:#8F8576;outline:none}.todo__check--checked{background-color:#806062}.todo>span{cursor:pointer}.todo__title{flex:1 1 auto;min-width:40px;padding-top:20px;padding-bottom:20px;max-height:60px;line-height:20px;overflow:hidden}.todo__description{flex:4 1 auto;max-height:60px;padding-top:20px;padding-bottom:20px;overflow:hidden;text-align:left}.todo__due-date{flex:1 0 auto;padding-top:20px;padding-bottom:20px;line-height:20px;overflow:hidden}.modal{position:fixed;z-index:1;left:0;top:0;height:100%;width:100%;overflow:auto;background-color:rgba(0,0,0,0.6);font-size:17.5px}.modal__content{background-color:#cbc6bd;width:75%;max-width:650px;margin:15% auto;border-radius:15px;padding:20px}.modal__header{text-align:center;font-size:28px}.modal__form{padding-top:10px;display:grid;grid-template-columns:auto 1fr;grid-gap:5px;gap:5px}.modal__label{text-align:right;padding:5px}.modal__input{background-color:#c0b9af;border:0;border-radius:5px;padding:10px;font-size:17.5px}.modal__btns{display:flex;flex-flow:row nowrap;justify-content:center;padding-top:20px}.modal__discard{background-color:#faac34;border:1px solid #f9a21b;max-width:100px;margin:5px 20px}.modal__discard:hover{box-shadow:0 0 5px 1px #f4cc8e}.modal__discard:active{box-shadow:inset 0 0 1px 1px #f59706}.modal__submit{background-color:#7dbc6c;border:1px solid #6eb45a;max-width:100px;margin:5px 20px}.modal__submit:hover{box-shadow:0 0 5px 1px #b5d4aa}.modal__submit:active{box-shadow:inset 0 0 1px 1px #60a84d}.modal__delete{background-color:#f44336;border:1px solid #f32c1e;max-width:100px;margin:5px 20px}.modal__delete:hover{box-shadow:0 0 5px 1px #f1978f}.modal__delete:active{box-shadow:inset 0 0 1px 1px #ea1c0d}.footer{padding:10px;background-color:#707A6B;color:#EDEBE8}

